    background-color: white;
    -webkit-animation: wiggle 8s ease-in-out infinite,reclistappear 3s ease-in-out;
    -moz-animation: wiggle 8s ease-in-out infinite,reclistappear 3s ease-in-out; 
    animation: wiggle 8s ease-in-out infinite, reclistappear 3s ease-in-out;
    overflow:hidden;
}

