.cardpressed {
    animation:  cardLiked 0.2s ease-in-out,wiggle 8s ease-in-out infinite;
}

@keyframes cardLiked {
    0% {    box-shadow: 0 2px 4px 0 rgba(0, 1, 0, 0.3), 0 3px 10px 0 rgba(0, 0, 0, 0.19);    }
    50%  { box-shadow: 0 1px 1px 0 rgba(0, 1, 0, 0.1), 0 1px 1px 0 rgba(0, 0, 0, 0.1); }
    100% {box-shadow: 0 2px 4px 0 rgba(0, 1, 0, 0.3), 0 3px 10px 0 rgba(0, 0, 0, 0.19);}
}

.rodal-dialog {
    width: 50vw;
    box-shadow: 0 2px 4px 0 rgba(0, 1, 0, 0.3), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
    min-height: 40vh;
    border-radius: 10px;
    padding: 20px;
    overflow-y:auto; 
    overflow-x:hidden;
    z-index:2000;
    transform: translateY(-200%); 
    height: 80vh;
    margin:auto;
    position:fixed;
    background-color: white;
}

.rodal-disappear {
    animation: slideAway 0.6s ease-in-out;

}

.rodal-appear {
    animation: slideUp 0.6s ease-in-out forwards;

}

.rodal-cancel {
    animation: slideCancel 0.6s ease-in-out;

}

.rodalmask-appear {
    animation: rodalMaskAppear 0.5s ease-in-out forwards;
    pointer-events: all;
}

.rodalmask-disappear {
    animation: rodalMaskAppear 0.5s ease-in-out reverse forwards;
    pointer-events: none;
}

@keyframes slideUp {
    0% { transform: translateY(200%); }
    88% { transform: translateY(-1%); }
    100% { transform: translateY(0%); }

  }

  @keyframes slideCancel {
    0% { transform: translateY(0%); }
    12% { transform: translateY(-1%); }
    100% { transform: translateY(200%); }

  }

  @keyframes slideAway {
    0% { transform: translateY(1%); }
    12% { transform: translateY(0%); }
    100% { transform: translateY(-200%); }

  }

  @keyframes rodalMaskAppear {
    0% {     opacity: 0;
        }
    100% {     opacity: 0.1;
        }
  }

  @keyframes rodalMaskDisappear {  
    0% {     opacity: 0.1; }
    100% {     opacity: 0;
        }
  }

  




