.iloveittext-pressed {
    animation: iloveittext-pressed 0.5s ease-in-out ;
}

.iloveittextbackround-pressed {
    animation: iloveittextbackround-pressed 0.5s ease-in-out forwards;
}

@media (pointer: coarse) {
    .iloveittext-pressed {
        animation: iloveittext-pressed 1s ease-in-out ;
    }
    
    .iloveittextbackround-pressed {
        animation: iloveittextbackround-pressed 1s ease-in-out forwards;
    }
}



@keyframes iloveittext-pressed {
    0% {
        color: black;
  
        -webkit-text-fill-color: white; /* Will override color (regardless of order) */
        -webkit-text-stroke-width: 2px;
        -webkit-text-stroke-color: black;
    }

    25% {
        opacity: 1;
    }
    60% {
        color:white;
        -webkit-text-fill-color: black; /* Will override color (regardless of order) */
        -webkit-text-stroke-width: 2px;
        -webkit-text-stroke-color: white;
    }
    85% {
        opacity: 1;
    }
    95% {
        color:white;
        -webkit-text-fill-color: black; /* Will override color (regardless of order) */
        -webkit-text-stroke-width: 2px;
        -webkit-text-stroke-color: white;
    }
    100% {
        opacity: 0;
    }
}

@keyframes iloveittextbackround-pressed {
    0% {
        width:0vmax;
        height:0vmax;
        opacity: 1;
    }
    60% {
        width:75vmax;
        height:75vmax;
        opacity: 1;
    }
    75% {
        opacity: 1;
    }
    95% {
        width:75vmax;
        height:75vmax;
    }
    100% {
        width:0vmax;
        height:0vmax;
        opacity: 0;
    }
}


